.p-breadcrumb {
  display: block;
  width: 100%;
  padding: rem(50) 0;
}

.p-breadcrumb__body span + .p-breadcrumb__body span {
  padding: 0 rem(5);
}

.p-breadcrumb__body ol {
  width: 100%;
  display: flex;
}

.p-breadcrumb__body li:nth-child(1)::after,
.p-breadcrumb__body li:nth-child(2)::after {
  content: '>';
  padding: 0 rem(5);
}
